<template>
  <v-container grid-list-xs fluid>
    <v-navigation-drawer
      ref="SlideBar"
      v-model="SlideBarToggle"
      v-clickoutside="handleClose"
      app
      clipped
      height="100%"
      :permanent="permanentSetting()"
      width="12rem"
    >
      <v-list class="py-0" close-on-click close-on-content-click>
        <v-list-item two-line class="px-2">
          <v-list-item-avatar>
            <img src="https://randomuser.me/api/portraits/men/81.jpg" />
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>
              {{User.nick_name}}
            </v-list-item-title>
          </v-list-item-content>
          <v-btn v-show="(User===undefined)">  </v-btn>

          <v-btn icon @click.stop="SlideBarToggle = !SlideBarToggle">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-list-content>
            {{User.introduction}}
          </v-list-content>
        </v-list-item>

        <v-list-item v-if="UserID" link>
          <v-list-item-content>
            <v-list-item-title class="title">  </v-list-item-title>
            <!-- <v-list-item-subtitle></v-list-item-subtitle> -->
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-divider />
      <v-divider />

      <v-list dense nav close-on-click close-on-content-click>
        <v-list-item
          v-for="item of List"
          :key="item.title"
          :to="{ name: item.link }"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <template v-slot:append>
        <v-list dense nav close-on-click close-on-content-click>
          <v-list-item link @click="logOut()">
            <v-list-item-icon>
              <v-icon>mdi-logout</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </template>
    </v-navigation-drawer>
  </v-container>
</template>

<script>
export default {
  name: "SlideBar",
  //
  directives: {
    //
    //v-clickoutside
    clickoutside: {
      // 
      bind(el, binding) {
        function documentHandler(e) {
          // 
          if (el.contains(e.target)) {
            return false;
          }
          // 
          if (binding.expression) {
            //  binding.valuehandleClose
            binding.value(e);
          }
        }
        // unbind
        el.__vueClickOutside__ = documentHandler;
        document.addEventListener("click", documentHandler);
      },
      unbind(el) {
        // 
        document.removeEventListener("click", el.__vueClickOutside__);
        delete el.__vueClickOutside__;
      },
    },
  },
  props: {
    SlideBarToggle: Boolean,
    List: Array,
    UserID: String,
    LogOutPathName: String,
    User:Object
  },
  data() {
    return {
    };
  },
  watch: {
    SlideBarToggle: function (val) {
      this.$emit("switchToggle", val);
    },
  },
  beforeDestroy() {
    // window.removeEventListener('resize', this.mdSetting, { passive: true })
  },
  mounted() {
    // window.addEventListener('resize', this.mdSetting, { passive: true })
    // console.log(this.$refs.SlideBar);
    // console.log(!this.expandOnHoverSetting());
    // console.log(this.$vuetify.breakpoint.name);
  },
  methods: {
    handleClose() {
      this.SlideBarToggle = false;
    },
    logOut() {
      this.$store.dispatch("logoutAction").then(() => {
        this.$router.push({ name: this.$props.LogOutPathName });
      });
    },
    expandOnHoverSetting() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return false;
          break;
        case "sm":
          return true;
          break;
        case "md":
          return true;
          break;
        case "lg":
          return true;
          break;
        default:
          return true;
          break;
      }
    },
    permanentSetting() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return false;
          break;
        case "sm":
          return true;
          break;
        case "md":
          return true;
          break;
        case "lg":
          return true;
          break;
        default:
          return true;
          break;
      }
    },
  },
};
</script>
<style lang="css">
/* .slideButton {
  margin: 4rem 0rem 0rem 1rem;
} */
</style>
